.home{
    display: flex;
    flex-direction: column;
    align-items: center;

  
    &-container{
        overflow: hidden;
        height: 100vh !important;

        &-navSearch {
            display: flex;
            flex-wrap: nowrap;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
        }
    }


    
    &-menu{
        // margin-top: 10px;
        height: 425px;
        width: 344px;
        left: 16px;
        top: 164px;
        border-radius: 28px;
        background-color: #F7F8FC;
        box-shadow: 0px 5px 17px rgba(38, 48, 115, 0.1), 0px -1px 10px rgba(38, 48, 115, 0.1);

        &-title{
            font-family: "Poppins";
            font-style: normal;
            font-weight: bold;
            font-size: 20px;
            line-height: 28px;
            text-align: center;
            margin-top: 28px;
            color: #19204D
            ;
        }

        &-buttons{
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;

            flex-wrap: wrap;
            &__camera{
                width: 128px;
                height: 128px;
                background: #F7F8FC;
                box-shadow: 0px 12px 4px -7px rgba(38, 48, 115, 0.2);
                border: none;
                border-radius: 50%;
                margin: 16px;
                display: flex;
                justify-content: center;
                
            }
            &__diagnosis{
                width: 128px;
                height: 128px;
                background: #F7F8FC;
                box-shadow: 0px 12px 4px -7px rgba(38, 48, 115, 0.2);
                border: none;
                border-radius: 50%;
                margin: 16px;
                display: flex;
                justify-content: center;
                
            }
            &__expert{
                width: 128px;
                height: 128px;
                background: #F7F8FC;
                box-shadow: 0px 12px 4px -7px rgba(38, 48, 115, 0.2);
                border: none;
                border-radius: 50%;
                margin: 16px;
                display: flex;
                justify-content: center;
                
            }
            &__aquarium{
                width: 128px;
                height: 128px;
                background: #F7F8FC;
                box-shadow: 0px 12px 4px -7px rgba(38, 48, 115, 0.2);
                border: none;
                border-radius: 50%;
                margin: 16px;
                display: flex;
                justify-content: center;

            }
        }
    }
    &-avatar{
        z-index: 9;
        position: relative;
        border: none;
        background-color: transparent;
        right: 65px;
        bottom: 12px;
    }

    &-search {
        margin-top: 100px;
    }

}
