.camera {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background-color: white;
  overflow: hidden;

  &-loading {
    height: 493px;
    width: 344px;
    border-radius: 28px;
    background: #f7f8fc;
    box-shadow: 0px 5px 17px rgba(38, 48, 115, 0.1),
      0px -1px 10px rgba(38, 48, 115, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;

    &-text {
      color: #263073;
      font-family: Poppins;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 24px;
      letter-spacing: 0em;
      text-align: center;
    }
  }

  &-album {
    background-color: transparent;
    border: none;
    position: relative;
    right: 240px;
    &-icon {
      width: 30px;
      color: #263073;
    }
  }
  &-webcam {
    padding: 25px;
    border-radius: 38px;
    background-color: white;
  }
  &-capture-icon {
    width: 80px !important;
    height: 80px !important;
    background-color: white;
    border-radius: 38px;
    display: flex;
    position: absolute;
    justify-content: center;
    align-items: center;
    bottom: 30px;
    z-index: 9;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    &__icon {
      width: 66px !important;
      height: 66px !important;
      background-color: white;
      border-radius: 40px;
      border: none;
    }
  }
  &-result {
    display: flex;
    justify-content: center;

    &-img {
      border-radius: 28px;
      width: 90%;
      height: auto;
      object-fit: scale-down;
    }
  }

  &-close {
    position: absolute;
    top: 50px;
    left: 15px;
    &-icon {
      background-color: transparent;
      border: none;
      color: #51e3d4;
      outline: none;
    }
  }
  &-refresh {
    position: absolute;
    top: 50px;
    right: 15px;
    z-index: 9;
    &-icon {
      background-color: transparent;
      border: none;
      color: #51e3d4;
      outline: none;
    }
  }

  &-switch {
    position: absolute;
    bottom: 26px;
    right: 40px;

    &-icon {
      background-color: transparent;
      border: none;
      color: #263073;
      outline: none;
    }
  }

  &-fishname {
    z-index: 9;
    position: absolute;
    top: 180px;

    & > a {
      text-decoration: none;
    }
    &-button {
      background: #ffffff;
      width: 240px;
      height: 56px;
      padding: 10px;
      box-shadow: 0px 4px 2px rgba(28, 176, 161, 0.2);
      border-radius: 0px 16px 16px 0px;
      border: none;
      display: flex;
      align-items: center;
      justify-content: center;
      font-family: "Poppins";
      font-style: normal;
      font-weight: 800;
      font-size: 16px;
      line-height: 24px;
      text-transform: uppercase;
      color: #263073;
      &__icon {
        position: relative;
        top: -40px;
        left: 200px;
        color: #1cb0a1;
        z-index: 9;
      }
    }

    &-img > img {
      border-radius: 28px;
      width: 64px;
      height: 64px;
      object-fit: cover;
      border: 3px solid #ffffff;
      box-sizing: border-box;
      z-index: 9;
      position: relative;
      bottom: 90px;
      right: 20px;
    }
  }

  &-no-identify {
    margin-top: 70px;
    // height: 100vh;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    &-background {
      background-color: #f7f8fc;
      width: 100%;
      border-radius: 28px;
    }
    &-button {
      width: 256px;
      margin-bottom: 10px;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      padding: 12px;
      background: #ffffff;
      border: 2px solid #ff8878;
      box-sizing: border-box;
      box-shadow: 0px 4px 2px rgba(153, 18, 0, 0.15);
      border-radius: 85px;
      font-family: Poppins;
      font-style: normal;
      font-weight: 800;
      font-size: 16px;
      line-height: 24px;
      display: flex;
      align-items: center;
      text-align: center;
      text-transform: uppercase;
      color: #ff8878;

      position: relative;
      bottom: 150px;
    }
  }
}
